.bandeau-container {
    width: 100%;
    height: auto;
}

.bandeau-content {
    display: block;
   
    @include externalPaddingMobile;
    padding-top: 8px;
    padding-bottom: 16px;
   
    @include desktop {
        @include externalPaddingDesktop;
        padding-top: 23px;
        padding-bottom: 23px;
    }
}

.bandeau-content__img {  
    width: 100%;
    object-position: 50% 50%;
    object-fit: cover;
    height: 111px;
    border-radius: 10px;
    -webkit-filter : brightness(70%);
    filter:brightness(70%);
   
    @include tablette-only {
        border-radius: 25px;
        height: 180px;
    }

    @include desktop {
        height: 223px;
    }
}